import { enableProdMode } from '@angular/core';
import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { getConfig, setConfig } from './app/app-config';

async function initialize() {
  if (getConfig().production) {
    enableProdMode();
  }

  const appModuleContainer = await import('./app/app.module');
  platformBrowserDynamic().bootstrapModule(appModuleContainer.AppModule)
    .catch(err => console.error(err));
}

fetch('/assets/app-config.json')
  .then(response => {
    if (response.ok) {
      return response.json();
    }
    throw new Error('app-config.json not found');
  })
  .then(config => {
    setConfig(config)
  })
  .catch(err => console.error(err))
  .finally(initialize)
